import React from "react";

export default class TableHeader extends React.Component {
    renderRow = () => {
        let arr = []
        switch (this.props.type) {
            case "statistic":
                arr.push(<th className="align-middle text-center" rowSpan="2">Lng</th>)
                break;
            case "management":
                arr.push(<th className="align-middle text-center" rowSpan="2">Sa</th>);
                arr.push(<th className="align-middle text-center" rowSpan="2">Xa</th>
                )
                break;

        }
        return arr;
    }
    renderRow2Header = () => {
        let arr = []
        switch (this.props.display) {
            case 1:
                arr.push(
                    <th className="align-middle text-center">Lng thng</th>);
                arr.push(
                    <th className="align-middle text-center">Bc lng</th>);
                arr.push(
                    <th className="align-middle text-center">Ph cp</th>);
                break;
            case 2:
                arr.push(
                    <th className="align-middle text-center">Lng ngy</th>);
                arr.push(
                    <th className="align-middle text-center">Ngy lm vic</th>);

                break;
            default:
                arr.push(
                    <th className="align-middle text-center">Lng thng</th>);
                arr.push(
                    <th className="align-middle text-center">Bc lng</th>);
                arr.push(
                    <th className="align-middle text-center">Ph cp</th>);
                arr.push(
                    <th className="align-middle text-center">Lng ngy</th>);

        }
        return arr;
    }
    renderTableHeader = () => {
        let arr = [];
        switch (this.props.display) {
            case 1:
                arr.push(<th className="align-middle text-center" colSpan="3">Nhn vin Bin ch</th>
                );
                break;
            case 2:
                arr.push(<th className="align-middle text-center" colSpan="2">Nhn vin Cng nht</th>)
                break;
            default:
                arr.push(<th className="align-middle text-center" colSpan="3">Nhn vin Bin ch</th>);
                arr.push(<th className="align-middle text-center">Nhn vin Cng nht</th>);
                break;

        }
        return arr
    }
    render = () => {
        return (
            <thead>
            <tr>
                <th className="align-middle text-center" rowSpan="2">#</th>
                <th className="align-middle text-center" rowSpan="2">Tn nhn vin</th>

                <th className="align-middle text-center" rowSpan="2">SDT</th>
                <th className="align-middle text-center" rowSpan="2">Ngy thng nm sinh</th>
                <th className="align-middle text-center" rowSpan="2">Phng ban</th>
                {this.renderTableHeader()}
                {
                    this.renderRow()
                }
            </tr>
            <tr>
                {
                    this.renderRow2Header()
                }

            </tr>
            </thead>
        )
    }
}

TableHeader.defaultProps = {
    hasSalary: false,
}


